html {
  font-size: 16px;
}

html,
body {
  height: 100%;
  overflow-x: hidden;
}

.container {
  margin: 0 auto;
  position: relative;
  width: 98%;
  margin: 0 auto;
  flex-direction: column;
  height: 100%;
}

.wrapper {
  position: relative;
  width: 100%;
  min-height: 100%;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  margin-top: 0;
  font-weight: normal;
}

ul {
  padding: 0;
  margin: 0;
}

ul li {
  list-style: none;
}

p {
  padding: 0;
  margin: 0;
}

a {
  text-decoration: none;
}

footer {
  position: relative;
  width: 100%;
}

* {
  box-sizing: border-box;
}

.header {
  display: flex;
  flex-direction: column;
  align-items: center;
  background-image: url("../img/bg.png");
  background-repeat: no-repeat;
  background-size: cover;
  height: 720px;
}

.header__container {
  background: rgba(0, 0, 0, 0.2);
  position: fixed;
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  z-index: 8888;
}

.header__container-active {
  opacity: 0;
}

.header__top-logo {
  margin-top: 20px;
}

.header__bottom-list {
  height: 30px;
  display: flex;
  justify-content: center;
  align-items: center;
  margin-top: 20px;
  margin-bottom: 20px;
}

.header__bottom-list a {
  color: #fff;
}

.header__bottom-item {
  margin-right: 20px;
  font-size: 18px;
}

.header__bottom-item:last-child {
  margin-right: 0;
}

.header__bottom-item:hover {
  transform: scale(1.1);
}

.content {
  margin-top: 30px;
}

.content__inf {
  width: 100%;
  height: 120px;
  background: rgba(106, 89, 158, 0.4);
}

.content__menu-list {
  display: flex;
  justify-content: space-between;
  margin-top: 15px;
  margin-bottom: 15px;
}

.content__menu-item {
  padding: 50px;
  width: 49.5%;
  display: flex;
  align-items: center;
  justify-content: center;
  background: rgba(106, 89, 158, 0.4);
}

.content__menu-pic:hover {
  transform: scale(1.1);
}

.content__product {
  width: 100%;
}

.content__product-list {
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 20px;
  margin-bottom: 15px;
  background: rgba(106, 89, 158, 0.4);
}

.content__product-item {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin: 10px;
  width: 20%;
}

.content__product-item:hover {
  transform: scale(1.1);
  transition: .1s;
}

.content__product-pic {
  display: flex;
  align-items: center;
  justify-content: center;
}

.content__product-img {
  width: 70%;
}

.content__product-name {
  text-align: center;
  margin-top: 5px;
  margin-bottom: 5px;
}

.footer {
  height: 120px;
  background: #6a599e;
}